<template>
  <v-app>
    <v-container class="mt-16">
      <v-row>
        <v-col cols="6">
          <v-card>
            <v-img :src="detallePelicula.poster" max-height="300" contain class="purple lighten-4"></v-img>
            <v-file-input accept="image/png, image/jpeg, image/bmp" prepend-icon="mdi-camera" v-model="poster"
              @change="subirFoto(detallePelicula._id)" placeholder="Cargar Imagen">

            </v-file-input>
            <v-card-title class="text-h4 justify-center font-weight-black font-italic">
              {{ this.detallePelicula.titulo }}
            </v-card-title>
          </v-card>
        </v-col>

        <v-col cols="6">
          <v-card-text class="font-weight-medium">{{ detallePelicula.sinopsis}} </v-card-text>
          <p class="mt-5"> Genero: {{ detallePelicula.genero }} </p>
          <p>Idioma: {{ detallePelicula.idioma }} </p>
          <p>Duracion: {{ detallePelicula.duracion }} </p>
          <p>Estado: {{ detallePelicula.estado}} </p>
          <p>Director: {{ detallePelicula.director }} </p>

          <v-card-action class="justify-center ">
            <v-btn rounded color="black" class="purple lighten-4">
              Play
            </v-btn>
            <v-btn rounded color="black" class="purple lighten-4">
              Agregar Reparto
            </v-btn>
            <v-btn icon color="pink">
              <v-icon>mdi-heart</v-icon>
            </v-btn>

          </v-card-action>


        </v-col>
      </v-row>
      <v-row>
        <v-col v-for="(p, i) in actor" :key="i">
          <v-card class="mx-auto mt-6" max-width="300">
            <v-img :src="p.foto" height="300px" ></v-img>
           
            <v-card-title >
              {{p.personaje}}
            </v-card-title>
            <v-card-subtitle>
              {{p.rol}}
            </v-card-subtitle>
           
         
          </v-card>

        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>
<script>
import axios from 'axios'
export default {
  name: "PagePelicula",
  data() {
    return {
      detallePelicula: [],
      poster: "",
      actor: [],
      reparto: []

    };
  },
  methods: {
    traerPeli() {
      this.detallePelicula = this.$store.state.datos;
      this.actor = this.$store.state.datos.reparto;
      console.log(this.actor)

    },
    subirFoto(id) {
      console.log(id);
      let ft= new FormData();
      ft.append("archivo", this.poster);
      console.log(ft);
      axios
        .post(
          `https://proyectmovie.herokuapp.com/api/pelicula/subir/${id}`,
          ft,
        )
        .then((response) => {
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });

    },
    mostrarActor() {

      axios.get(`https://proyectmovie.herokuapp.com/api/actor/mostrar/id/${this.actor[0].idActor}`)
        .then((response) => {
          console.log(response.data.actor);
          this.reparto = response.data.actor
          console.log(this.reparto)

        })
        .catch((error) => {
          console.log(error)
        })
    },
    
    
  },
  
  created() {
    this.traerPeli();
    this.mostrarActor()
  },

};
</script>

