<template>
  <v-app>
    <v-container >
      <v-layout >
        <v-flex >
          <v-card  class=" mx-auto elevation-24 pa-12 ma-16 text-center purple lighten-5 text-no-wrap rounded-lg" max-width="800">
            <v-toolbar justify="center" class="purple lighten-3" >
              <v-toolbar-title  class="text-center font-italic font-weight-black display-1">Añadir otra Cuenta ►</v-toolbar-title>
              
            </v-toolbar>
         

              <v-text-field
                background-color=""
                v-model="nombre"
                label="Nombre"
                type="text"
              ></v-text-field>
              <v-text-field
                background-color=""
                v-model="apellidos"
                label="Apellidos"
                type="text"
              ></v-text-field>
              <v-text-field
                background-color=""
                v-model="email"
                id="email"
                name="email"
                label="Email"
                type="text"
              ></v-text-field>
              <v-text-field
                background-color=""
                v-model="password"
                id="password"
                name="password"
                label="Password"
                type="password"
              ></v-text-field>
              <h5>Al hacer click en el boton "Registrar", certifico que he leido y acepto los terminos de uso y la politica de privacidad.</h5>
              <br>
              <v-btn  justify="center" class="pa-6 purple lighten-3 text-no-wrap rounded-pill"   @click="agregar()"
                >Registrar</v-btn>
               <v-btn  justify="center" class="pa-6 ma-4 purple lighten-3 text-no-wrap rounded-pill"   @click="clear()"
                >Cancelar</v-btn>
            
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>

  </v-app>
</template>

<script>
import axios from "axios";
export default {
  name: "PageRegistro",

  data: () => ({
    checkbox: null,
   
      nombre:"",
      apellidos:"",
      email:"",
      password:"",
      foto:[]
      
   
  }),

  methods: {
    // submit() {
    //   this.$refs.observer.validate();
    // },
    clear() {
      this.nombre = "";
      this.apellidos = "";
      this.email = "";
      this.password = "";

      this.$refs.observer.reset();
    },
    agregar(){
      
      axios
      .post("https://proyectmovie.herokuapp.com/api/persona",{

        nombre:this.nombre,
        apellido:this.apellidos,
        email:this.email,
        password:this.password
      })
      .then((response)=>{
    
        console.log(response)
    
      })
      .catch((error) => {
          console.log(error);
        });

    },
    
  },
  created(){
    this.agregar();
  }
};
</script>
<style>

.login {
  border: 1px;
  border-radius: 5px;
  padding: 100px;
}
