import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export const store = new Vuex.Store({
    state:{
        token:"",
        datos:{},
        action:[],
        peliculas:[],
        personas:{},
        foto:[],
    
        reparto:[],
        agregarFavorito:[],
        desserts:[]
        


    },

    mutations:{
        setToken(state, value){
            state.token=value
        },
        setDatos(state, value){
            state.datos=value
        },
        setPelicula(state,v){
            state.peliculas=v
        },
        setMovie(state,v){
            state.movies=v
        },
        setDatosUsuario(state,v){
            state.personas=v
        },
        setAction(state,v){
            state.action.push(v);
        },
        setReparto(state, value){
            state.reparto.push(value)
        },
        setAgregarFavorito(state,v){
            state.agregarFavorito=v
        },
       
        
        
    },
    actions:{
        setToken(context, value){
            context.commit("setToken", value)
        },
        setDatos(context,value){
            context.commit("setDatos", value)
        },
        setPeliculas(context,value){
            context.commit("setPeliculas",value)
        },
        setMovies(context,value){
            context.commit("setMovies",value)
        },
        setDatosUsuario(context,value){
            context.commit("setDatosUsuario",value)
        }
        

    }

    
})