<template>
  <v-app>
    <v-carousel class="mb-16">
      <v-carousel-item v-for="(item, i) in action" :key="i" :src="item.poster" reverse-transition="fade-transition"
        transition="fade-transition" @click="detallePelicula(item)"></v-carousel-item>
    </v-carousel>
    <v-container class="mt-5">
      <v-row>
        <v-col v-for="(p, i) in action" :key="i">
          <v-card :loading="loading" class="mx-n6 my-2" max-width="374" height="580">
            <template slot="progress">
              <v-progress-linear color="deep-purple" height="10" indeterminate></v-progress-linear>
            </template>

            <v-img height="250" :src="p.poster"></v-img>
            <v-row class="mx-2 my-2">
              <v-rating :value="4.5" color="amber" dense half-increments readonly size="14"></v-rating>

              <div class="grey--text ms-4">4.5 (413)</div>
            </v-row>

            <v-card-title class="justify-center font-italic font-weight-black">{{ p.titulo }}</v-card-title>
            <v-card-subtitle> Genero: {{ p.genero }}</v-card-subtitle>

            <v-card-text>
              <div>{{ p.sinopsis }}.</div>
            </v-card-text>
            <v-card-actions >
              <v-btn text color="deep-purple lighten-3 "  @click="detallePelicula(p)">Mas Detalles </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>
 
<script>
import axios from "axios";
export default {
  name: "PageHome",

  data: () => ({

    action: [],
  }),
  methods: {
    traerPeliculas() {
      let header = { headers: { "x-token": this.$store.state.token } };

      axios
        .get("https://proyectmovie.herokuapp.com/api/pelicula", header)

        .then((response) => {
          console.log(response);
          this.action = response.data.peliculas;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    detallePelicula(item) {
      this.$router.push("/pelicula");
      this.$store.dispatch("setDatos", item);
      console.log(this.$store.state.datos);
    },
  },
  created() {
    this.traerPeliculas();
  },
};
</script>
