<template>
  <v-app>
    <v-container>
      <v-row>
        <v-col>
          <v-card class=" mx-auto elevation-24 pa-12 ma-16 text-center purple lighten-5 text-no-wrap rounded-lg" max-width="800">
            <v-form >
            <v-text-field background-color="" v-model="nombre" label="Nombre" type="text"></v-text-field>
          <v-text-field background-color="" v-model="alias" label="Alias" type="text"></v-text-field>
          <v-text-field background-color="" v-model="biografia" label="Biografia" type="text"></v-text-field>
          <v-btn  class="pa-6 purple lighten-3 text-no-wrap rounded-pill" @click="agregarActor()">
            Agregar Actor</v-btn>

          </v-form>

          </v-card>
         
        


        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>
<script>
import axios from 'axios'
export default {
  name: "PageNewactor",
  data() {
    return {
      nombre: "",
      alias: "",
      biografia: "",

    }
  },
  methods: {
    agregarActor() {

      axios
        .post("https://proyectmovie.herokuapp.com/api/actor", {

          nombre: this.nombre,
          alias: this.alias,
          biografia: this.biografia,


        })
        .then((response) => {

          console.log(response)

        })
        .catch((error) => {
          console.log(error);
        });

    },


  },
  creted() {
    this.agregarActor();
  }
}
</script>