<template>
  <v-app>
    <v-container fluid>
     
          <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr class="purple lighten-5">
          <th class="text-left">
            ID
          </th>
          <th class="text-left">
            Titulo
          </th>
          <th class="text-left">
            Duracion
          </th>
          <th class="text-left">
            Poster
          </th>
          <th class="text-left">
            Genero
          </th>
          <th class="text-left">
            Sinopsis
          </th>
          <th class="text-left">
            Estado
          </th>
          <th class="text-left">
            Idioma
          </th>
          <th class="text-left">
            Director
          </th>
          <th class="text-left">
            Reparto
          </th>
          <th class="text-left">
            Eliminar
          </th>
          
          
          
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in desserts"
          :key="item._id"
        >
        <td class="purple lighten-4" >{{ item._id}}</td>
          <td class="purple lighten-3" >{{ item.titulo}}</td>
          <td class="purple lighten-4">{{ item.duracion }}</td>
          <td class="purple lighten-3">{{ item.poster }}</td>
          <td class="purple lighten-4">{{ item.genero }}</td>
          <td class="purple lighten-3">{{ item.sinopsis }}</td>
          <td class="purple lighten-4">{{ item.estado }}</td>
          <td class="purple lighten-3">{{ item.idioma }}</td>
          <td class="purple lighten-4">{{ item.director}}</td>
          <td class="purple lighten-3">{{ item.reparto }}</td>

          <td class="purple lighten-4"><v-btn @click="deleteItem(item._id)">❌</v-btn></td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
  

       
    </v-container>
  </v-app>
 
</template>
<script>
  import axios from 'axios';
 export default {

name: "PageEliminar",
data() {
  return {
    dialog: false,
      dialogDelete: false,
      headers: [

       { text: 'ID', value: 'id' },
        { text: 'Titulo', value: 'titulo' },
        { text: 'Duracion', value: 'duracion' },
        { text: 'Poster', value: 'poster' },
        { text: 'Genero', value: 'genero' },
        { text: 'Sinopsis', value: 'sinopsis' },
        { text: 'Estado', value: 'estado' },
        { text: 'Idioma', value: 'idioma' },
        { text: 'Director', value: 'director' },
        // { text: 'Reparto Actor', value: 'reparto' },
        // { text: 'Personaje', value: 'Personaje' },
        // { text: 'Rol', value: 'rol' },
        { text: 'Actions', value: 'actions' },
      ],
      desserts: [],
    
     
      
  }
},
methods: {
  traerPeli() {
      let header = { headers: { "x-token": this.$store.state.token } };

      axios
        .get("https://proyectmovie.herokuapp.com/api/pelicula", header)

        .then((response) => {
          console.log(response);
          this.desserts = response.data.peliculas;
      
        })
        .catch((error) => {
          console.log(error);
        });
    },
    deleteItem(id){
     if(id){
      axios
      .delete(`https://proyectmovie.herokuapp.com/api/pelicula/${id}`)
      .then((response) => {
        
          console.log(response);
          
        })
        .catch((error) => {
          console.log(error);
        });

    }
  },
   
 
},
created() {
    this.traerPeli();
    
  },
}
</script>
