<template>
  <v-app>
    <v-container>
      <v-layout>
        <v-flex>
          <v-card class="mx-auto elevation-24 text-center purple lighten-5 text-no-wrap rounded-lg"
            style="max-width: 800px;">
            <v-system-bar color="purple lighten-3" dark>
              <v-spacer></v-spacer>
              <v-icon>mdi-window-minimize</v-icon>
              <v-icon>mdi-window-maximize</v-icon>
              <v-icon>mdi-close</v-icon>
              <span>12:30</span>
            </v-system-bar>
            <v-toolbar color="purple lighten-4" cards dark flat>
              <h3 class="font-italic">REGISTRO NUEVA PELICULA</h3>
            </v-toolbar>
            <v-form ref="form" v-model="form" class="pa-4 pt-6">

              <v-text-field v-model="titulo" label="Titulo" outlined></v-text-field>
              <v-text-field v-model="duracion" label="Duracion" outlined></v-text-field>
              <v-text-field v-model="genero" label="Genero" outlined></v-text-field>
              <v-textarea outlined name="input-7-4" label="Sinopsis" v-model="sinopsis"></v-textarea>
              <v-text-field v-model="estado" label="Estado" outlined></v-text-field>
              <v-text-field v-model="idioma" label="Idioma" outlined></v-text-field>
              <v-text-field v-model="director" label="Director" outlined></v-text-field>
              <template>
                <v-row justify="center">
                  <v-dialog v-model="dialog" width="600px">
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn color="purple lighten-3" dark v-bind="attrs" v-on="on">
                        Agregar Reparto
                      </v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="text-h5">Seleccione la lista de actores</span>
                      </v-card-title>
                      <v-col v-for="(p, i) in actor" :key="i">
                        <v-card>
                          <v-img :src="p.foto" height="250px"></v-img>
                          <v-btn class="primary" @click="capturarActor(p._id)">agregar reparto</v-btn>

                        </v-card>
                      </v-col>
                      <v-text-field v-model="personaje" label="Personaje" outlined></v-text-field>
                      <v-text-field v-model="rol" label="Rol" outlined></v-text-field>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                       
                        <v-btn color="green darken-1" text @click="subirReparto">
                          Agregar
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-row>
              </template>

              <v-btn justify="center" rounded color="purple lighten-3 mt-5" dark @click="agregarMovie()">
                Subir Pelicula
              </v-btn>

            </v-form>
          </v-card>

        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>
<script>
import axios from 'axios'
export default {
  name: "PageNewmovie",
  data() {
    return {
      dialog: false,
      titulo: "",
      duracion: "",
      genero: "",
      sinopsis: "",
      estado: "",
      idioma: "",
      director: "",
      reparto: [],
      actor: {},
      personaje: "",
      rol: "",
      id: "",
      
    }
  },
  methods: {

    agregarMovie(){
     
      axios
        .post("https://proyectmovie.herokuapp.com/api/pelicula", {
          titulo: this.titulo,
          duracion: this.duracion,
          gereno: this.genero,
          sinopsis: this.sinopsis,
          estado: this.estado,
          idioma: this.idioma,
          director: this.director,
          reparto: this.$store.state.reparto
          

        })
        .then((response) => {

          console.log(response)

        })
        .catch((error) => {
          console.log(error);
        });

    }, traerActor() {

      axios
        .get("https://proyectmovie.herokuapp.com/api/actor")

        .then((response) => {

          this.actor = response.data.actores;
          console.log(this.actor);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    capturarActor(id) {
      this.id = id
      console.log(this.id)


    },
    subirReparto() {

      let idActor = this.id
      this.reparto.push(
        {
          idActor: idActor,
          personaje: this.personaje,
          rol: this.rol
        }
      )
      console.log(this.reparto)
    }
  },
  created() {

    this.traerActor(),
      this.agregarMovie(),
      this.subirReparto()
  }
}



</script>