<template>
    <v-app>
      <v-container fluid>
       
            <v-simple-table>
      <template v-slot:default>
        <thead>
          <tr class="purple lighten-5">
            <th class="text-left">
              ID
            </th>
            <th class="text-left">
              Titulo
            </th>
            <th class="text-left">
              Duracion
            </th>
            <th class="text-left">
              Poster
            </th>
            <th class="text-left">
              Genero
            </th>
            <th class="text-left">
              Sinopsis
            </th>
            <th class="text-left">
              Estado
            </th>
            <th class="text-left">
              Idioma
            </th>
            <th class="text-left">
              Director
            </th>
            <th class="text-left">
              Reparto
            </th>
            <th class="text-left">
              Eliminar
            </th>
            
            
            
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in desserts"
            :key="item._id"
          >
          <td class="purple lighten-4">{{ item._id}}</td>
            <td class="purple lighten-3" >{{ item.titulo}}</td>
            <td class="purple lighten-4">{{ item.duracion }}</td>
            <td class="purple lighten-3">{{ item.poster }}</td>
            <td class="purple lighten-4">{{ item.genero }}</td>
            <td class="purple lighten-3">{{ item.sinopsis }}</td>
            <td class="purple lighten-4">{{ item.estado }}</td>
            <td class="purple lighten-3">{{ item.idioma }}</td>
            <td class="purple lighten-4">{{ item.director}}</td>
            <td class="purple lighten-3">{{ item.reparto }}</td>
  
            <td class="purple lighten-4"><v-btn @click.stop="dialog = true">Editar</v-btn></td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
    <v-dialog
      v-model="dialog"
      width="600px"

    >

      <v-card class="text-center" >
        <v-card-title>
          <span class="text-h5">Editar Pelicula</span>
        </v-card-title>
        <v-form ref="form" v-model="form" class="pa-4 pt-6" >

<v-text-field v-model="titulo" label="Titulo" outlined></v-text-field>
<v-text-field v-model="duracion" label="Duracion" outlined></v-text-field>
<v-text-field v-model="genero" label="Genero" outlined></v-text-field>
<v-textarea outlined name="input-7-4" label="Sinopsis" v-model="sinopsis"></v-textarea>
<v-text-field v-model="estado" label="Estado" outlined></v-text-field>
<v-text-field v-model="idioma" label="Idioma" outlined></v-text-field>
<v-text-field v-model="director" label="Director" outlined></v-text-field>


<v-btn justify="center" rounded color="purple lighten-3 mt-5" dark @click="editarItem()">
  Editar Pelicula
</v-btn>

</v-form>     <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="black darken-1"
            text
            @click="dialog = false"
          >
            Cerrar
          </v-btn>
          
        </v-card-actions>
      </v-card>
    </v-dialog>
  
         
      </v-container>
    </v-app>
   
  </template>
  <script>
    import axios from 'axios';
   export default {
  
  name: "PageEditar",
  data() {
    return {
      dialog: false,
        dialogDelete: false,
 
        titulo:"",
          duracion:"",
          gereno: "",
          sinopsis: "",
          estado:"",
          idioma:"",
          director:"",
          desserts:[],
          detalle:[]
         
         
      
       
        
    }
  },
  methods: {

    traerPeli() {
        let header = { headers: { "x-token": this.$store.state.token } };
        axios
          .get("https://proyectmovie.herokuapp.com/api/pelicula",header,)
  
          .then((response) => {
            console.log(response);
            this.desserts = response.data.peliculas;
        
          })
          .catch((error) => {
            console.log(error);
          });
      },
      detallePelicula(item) {
      this.$router.push("/pelicula");
      this.$store.dispatch("setDatos", item);
      console.log(this.$store.state.datos);
    },
      
     
   
      editarItem(id){
       if(id){
        axios
        .put(`https://proyectmovie.herokuapp.com/api/pelicula/${id}`,{
          titulo: this.titulo,
          duracion: this.duracion,
          gereno: this.genero,
          sinopsis: this.sinopsis,
          estado: this.estado,
          idioma: this.idioma,
          director: this.director,
          reparto: this.reparto
        })
        .then((response) => {
          
            console.log(response);
            
          })
          .catch((error) => {
            console.log(error);
          });
  
      }
    },
     
   
  },
  created() {
      this.traerPeli();
     
      
    },
  }
  </script>