<template>
  <v-app>
    <v-container >
      <v-row>
        <v-col class="mt-10"  v-for="(p, i) in reparto" :key="i" >
          <v-card class="mx-auto mt-n5" max-width="300" >
            <v-img :src="p.foto" height="300px"></v-img>
            <v-file-input accept="image/png, image/jpeg, image/bmp" prepend-icon="mdi-camera" v-model="foto"
              @change="subirFoto(p._id)" placeholder="Cargar Imagen">
            </v-file-input>
            <v-card-title>
              {{p.nombre}}
            </v-card-title>
            <v-card-subtitle>
              {{p.alias}}
            </v-card-subtitle>
            <v-card-actions>
              <v-btn color="orange lighten-2" text>
                Biografia
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn icon @click="show = !show">
                <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
              </v-btn>
            </v-card-actions>
            <v-expand-transition>
              <div v-show="show">
                <v-divider></v-divider>
                <v-card-text>
                  {{p.biografia}}
                </v-card-text>
              </div>
            </v-expand-transition>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>
<script>
import axios from "axios";
export default {
  name: "PageActor",
  data() {
    return {
      reparto: [],
      foto: "",
      show: false,
    }
  },
  methods: {
    traerActor() {


      axios
        .get("https://proyectmovie.herokuapp.com/api/actor")

        .then((response) => {
          console.log(response);
          this.reparto = response.data.actores;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    subirFoto(id) {
      console.log(id);
      let fd = new FormData();
      fd.append("archivo", this.foto);
      console.log(fd);
      axios
        .post(
          `https://proyectmovie.herokuapp.com/api/actor/subir/`,
          fd,
        )
        .then((response) => {
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });

    },
  },
  created() {
    this.traerActor();
  },
}
</script>